import './TodoItem.css';
import {Reorder, useDragControls} from "framer-motion";
import {useDispatch, useSelector} from "react-redux";
import {setIsEditTaskBlock, setSelectTask} from "../../../redux/reducers/tasks";
import tasks_ok_icon from '../../../foto/task_ok.png';
import tasks_no_icon from '../../../foto/task_no.png';
import {useCallback, useState} from "react";
import debounce from "lodash/debounce";

export const TodoItem = ({item}) => {

  const controls = useDragControls();
  const dispatch = useDispatch();
  const isEditTaskBlock = false

  const onSetSelectTask = (id, selectTask) => {
    dispatch(setSelectTask({id, selectTask}))
  };


  const [touchCenter, setTouchCenter] = useState(null);
  const max = 80;
  const min = 70;

  const onTouchStartDelete = (e) => setTouchCenter(e.changedTouches[0].clientX - e.currentTarget.getBoundingClientRect().left);

  const onTouchMoveDelete = (e) => {
    const widthDifference = touchCenter - e.changedTouches[0].clientX;
    if(widthDifference >= 0 && widthDifference <= max) e.currentTarget.style.right = widthDifference + 'px';
  };

  const onTouchEndDelete = (e) => {
    const widthDifference = touchCenter - e.changedTouches[0].clientX;
    const right = widthDifference >= min ? min : 0;
    e.currentTarget.style.right = right+ 'px';
  };

  return (
    <Reorder.Item
      key={item.id}
      value={item}
      className='TaskItemWrapper'
      dragListener={isEditTaskBlock}
      dragControls={controls}
    >
      <div className='btnDeleteItem'></div>
      <button
        className='TaskItem'
        onTouchMove={onTouchMoveDelete}
        onTouchEnd={onTouchEndDelete}
        onTouchStart={onTouchStartDelete}
        onBlur={(e) => e.currentTarget.style.right = 0 + 'px'}
      >
        {!isEditTaskBlock && <div className='Btn-task-finish'></div>}
        {isEditTaskBlock && <BtnItemReorder onPointerDown={(e) => controls.start(e)}/>}
        <p className='Task-Item-content'>{item.name}</p>
        {isEditTaskBlock &&
          <div
            className='checkbox-choose-task'
            onClick={() => onSetSelectTask(item.id, !item.selectTask)}
          >
            <img src={item.selectTask ? tasks_ok_icon : tasks_no_icon} alt='circle'/>
          </div>
         }
      </button>
    </Reorder.Item>
  );
};

const BtnItemReorder = (props) => {
  return (
    <div
      className="reorder-handle"
      {...props}
    >
      <div className='reorder-handle-item rh1'></div>
      <div className='reorder-handle-item rh2'></div>
      <div className='reorder-handle-item rh3'></div>
    </div>
  )
};


